<template>
	<div class="infoarticlewrap">
		<h2 class="infoarticletitle">三种不自律的表现，足够毁了我</h2>
        <div class="statisticswrap">
            <!-- f -->
            <span>发布时间：2019-5-20</span>
                <span id="busuanzi_container_page_pv">浏览：<span id="busuanzi_value_page_pv">0</span></span>
            </div>
    		<div class="infoarticlecontent">
                <p><b>拖延症</b></p><p>我是一个严重的拖延症患者，拖延到什么程度？</p><p>礼拜是让人最放松的日子，上班比较劳累，一般大家都会选择在这个日子去放松自己，周末前，我给自己列了个计划，要去橘子洲头或者岳麓山逛一圈，让自己透透空气，因为自己从事的是软件行业，久坐不起，都感觉长痔疮了，可是一到周末，朋友突然来了个电话，问我要不要撸两把，我犹豫了一下，想想反正现在才10点多，没事下午去橘子洲头也来得及，哈哈！没有想到一撸就是一天，连中饭都是外卖解决的。</p><p>自己的身体不太好，每天都要告诉自己要早睡早起，可是最终没能逃脱手机的控制，一看时间，凌晨2:00。</p><div class="image-package">
            <img src="https://upload-images.jianshu.io/upload_images/16263448-d1af5468f4d38c0d?imageMogr2/auto-orient/strip%7CimageView2/2/w/600/format/webp">
            <div class="image-caption"></div>
            </div><p>时刻告诉自己，早上起来要喝杯温开水，天气好的时候出去跑跑，天气不好的时候在家做做仰卧起坐，可是每次闹钟一响，不自觉的盖着被子，心想再眯一会，就一分钟，然后看了下时间8:40，嚯！！！怎么只剩20分钟了。</p><p>好不容易鼓起勇气去书店买了一本书，两个月后发现，诶！这书啥时候买的？</p><p>直到有一天，身体给自己发出了严重的警告，随之而来的肥胖、雀斑、细纹、耳鸣、便秘...才开始反省自己，过去的日子我到底做了些什么？</p><p>拖延是不自律最经典的表现，一不小心就会拖走青春，拖走健康，更别说财富了。</p><p><b>借口多</b></p><p>要不是今天下雨，我肯定会早起运动的。</p><p>今天太阳太大了，出去还不得晒晕，没事，等天气好点了我再出去散散心。</p><p>我今天上了一天的班，已经累得不行，我就玩一把游戏放松一下，不碍事，明天我再找个时间安静的看会书。</p><p>我真的不喜欢吃蔬菜，不知道为什么，就是不喜欢吃。</p><p>我要减肥，今晚我决定不吃东西，结果“从天而降”一碗美食，香气逼人，诱惑十足，然后快准狠的把它“干了！”。</p><div class="image-package">
            <img src="https://upload-images.jianshu.io/upload_images/16263448-f7b21fff277fe9c0?imageMogr2/auto-orient/strip%7CimageView2/2/w/600/format/webp">
            <div class="image-caption"></div>
            </div><p>借口多了可是会形成习惯的，对事物总会找出借口来，这好像是上天赋予我的本能，不去找借口浑身都难受，借口多了，失去的机会就多了，机会少了，成功的几率就低了，遗憾的事情也就多了，比起长久的遗憾，不如短暂的痛苦。</p><p><b>迷茫</b></p><div class="image-package">
            <img src="https://upload-images.jianshu.io/upload_images/16263448-417f43eff10a9733?imageMogr2/auto-orient/strip%7CimageView2/2/w/600/format/webp">
            <div class="image-caption"></div>
            </div><p>不自律的人往往会陷入迷茫，不知所向，在自己的舒适区内安详，想想未来这样的自己，有些后怕，可是当下却不敢迈出跳槽的步伐。</p><p>时间长了，产生的焦虑都快淹没自己前进的动力，然后选择逃避。</p><p>想想都可悲...</p><p>但是生活总是充满着希望的，当身体发出警告，当经济面临危机，当幸福感跌落谷底，这时还不觉醒，顺势脱离苦海，更待何时？</p><p>生命唯有一次，懂得自律的人才配拥有自由，健康的自由、财富的自由、追求幸福的自由...</p><p>如果你踩中了这些雷区，赶紧“回头”吧！前面可是深渊...</p><p>未经本人同意，请勿转载，谢谢哦！</p>
